import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { AuthService } from '../services/auth/auth.service';
import { Router } from '@angular/router';
import { map, switchMap } from 'rxjs/operators';
import { Observable, combineLatest, of } from 'rxjs';
import { firestore } from 'firebase/app';

@Injectable({
  providedIn: 'root'
})
export class ChatModalService {

  constructor(
    private afs: AngularFirestore,
    private auth: AuthService,
    private router: Router) { }

  get(chatId) {
    return this.afs
      .collection<any>('chats')
      .doc(chatId)
      .snapshotChanges()
      .pipe(
        map(doc => {
          return { id: doc.payload.id, ...doc.payload.data() };
        })
      );
  }

  get2(chatId: string) {
    const chatRef = this.afs.doc(`chats/${chatId}`);
    return chatRef.get();
  }

  async create(chatId) {
    const { uid } = await this.auth.getUser();
    const chatRef = this.afs.doc(`chats/${chatId}`);

    const data = {
      uid,
      createdAt: Date.now(),
      count: 0,
      messages: []
    };

    return chatRef.set(data);
    //const docRef = await this.afs.collection('chats').add(data);
    //return this.router.navigate(['chats', docRef.id]);
  }
  
  async sendMessage(chatId, content) {
    const { uid } = await this.auth.getUser();

    const data = {
      uid,
      content,
      createdAt: Date.now()
    };

    if (uid) {
      const ref = this.afs.collection('chats').doc(chatId);
      return ref.update({
        messages: firestore.FieldValue.arrayUnion(data)
      });
    }
  }

  /******************************************************************
   * Joins users Grabs the unique IDs from the chat messages array, 
   * then joins the user profile data to each message and keeps 
   * the entire payload synced in realtime.
   *****************************************************************/
  joinUsers(chat$: Observable<any>) {
    let chat;
    const joinKeys = {};
  
    return chat$.pipe(
      switchMap(c => {
        // Unique User IDs
        chat = c;
        const uids = Array.from(new Set(c.messages.map(v => v.uid)));
  
        // Firestore User Doc Reads
        const userDocs = uids.map(u =>
          this.afs.doc(`profiles/${u}`).valueChanges()
        );
  
        return userDocs.length ? combineLatest(userDocs) : of([]);
      }),
      map(arr => {
        arr.forEach(v => (joinKeys[(<any>v).uid] = v));
        chat.messages = chat.messages.map(v => {
          return { ...v, user: joinKeys[v.uid] };
        });
  
        return chat;
      })
    );
  } // end of join users

}
